
'use client';

import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from '@/components/ui/alert-dialog';
import React, { createContext, useContext, useState, ReactNode, useCallback } from 'react';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';

interface WalletState {
  isConnected: boolean;
  walletAddress: string | null;
  connectWallet: () => void;
  disconnectWallet: () => void;
}

const WalletContext = createContext<WalletState | undefined>(undefined);

export const WalletProvider = ({ children }: { children: ReactNode }) => {
  const [isConnected, setIsConnected] = useState(false);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [isConnecting, setIsConnecting] = useState(false);
  const [inputWalletAddress, setInputWalletAddress] = useState('');
  const { toast } = useToast();

  const connectWallet = useCallback(() => {
    setIsConnecting(true);
  }, []);

  const handleConnectConfirm = () => {
    if (!/^0x[a-fA-F0-9]{40}$/.test(inputWalletAddress)) {
      toast({
        title: 'Invalid Address',
        description: 'Please enter a valid 42-character hexadecimal address starting with 0x.',
        variant: 'destructive',
      });
      return;
    }

    setWalletAddress(inputWalletAddress);
    setIsConnected(true);
    handleConnectCancel(); // Close dialog and reset fields
  };

  const handleConnectCancel = () => {
    setIsConnecting(false);
    setInputWalletAddress('');
  };

  const disconnectWallet = () => {
    setWalletAddress(null);
    setIsConnected(false);
  };

  return (
    <WalletContext.Provider
      value={{ isConnected, walletAddress, connectWallet, disconnectWallet }}
    >
      {children}
      {isConnecting && (
         <AlertDialog open onOpenChange={setIsConnecting}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Connect to a Wallet</AlertDialogTitle>
              <AlertDialogDescription>
                This is a simulation. Enter a 42-character address to proceed.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <div className="space-y-4 py-4">
              <div className="grid gap-2">
                <Label htmlFor="wallet-address">Wallet Address</Label>
                <Input
                  id="wallet-address"
                  placeholder="e.g., 0xAbCd1234EfgH5678iJkl90mNoP1234qRs56789T0"
                  value={inputWalletAddress}
                  onChange={(e) => setInputWalletAddress(e.target.value)}
                  maxLength={42}
                />
              </div>
            </div>
            <AlertDialogFooter>
              <AlertDialogCancel onClick={handleConnectCancel}>Cancel</AlertDialogCancel>
              <AlertDialogAction onClick={handleConnectConfirm}>Connect</AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      )}
    </WalletContext.Provider>
  );
};

export const useWallet = () => {
  const context = useContext(WalletContext);
  if (context === undefined) {
    throw new Error('useWallet must be used within a WalletProvider');
  }
  return context;
};
